	module.exports = {"pathContext":{"docHtml":"<h1>parse-passwd <a href=\"https://www.npmjs.com/package/parse-passwd\"><img src=\"https://img.shields.io/npm/v/parse-passwd.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/parse-passwd\"><img src=\"https://img.shields.io/npm/dm/parse-passwd.svg?style=flat\" alt=\"NPM downloads\"></a> <a href=\"https://travis-ci.org/doowb/parse-passwd\"><img src=\"https://img.shields.io/travis/doowb/parse-passwd.svg?style=flat&#x26;label=Travis\" alt=\"Linux Build Status\"></a> <a href=\"https://ci.appveyor.com/project/doowb/parse-passwd\"><img src=\"https://img.shields.io/appveyor/ci/doowb/parse-passwd.svg?style=flat&#x26;label=AppVeyor\" alt=\"Windows Build Status\"></a></h1>\n<blockquote>\n<p>Parse a passwd file into a list of users.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install --save parse-passwd\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var parse = require('parse-passwd');\n</code></pre>\n<h2>API</h2>\n<p><strong>Example</strong></p>\n<pre><code class=\"language-js\">// assuming '/etc/passwd' contains:\n// doowb:*:123:123:Brian Woodward:/Users/doowb:/bin/bash\nconsole.log(parse(fs.readFileSync('/etc/passwd', 'utf8')));\n\n//=> [\n//=>   {\n//=>     username: 'doowb',\n//=>     password: '*',\n//=>     uid: '123',\n//=>     gid: '123',\n//=>     gecos: 'Brian Woodward',\n//=>     homedir: '/Users/doowb',\n//=>     shell: '/bin/bash'\n//=>   }\n//=> ]\n</code></pre>\n<p><strong>Params</strong></p>\n<ul>\n<li><code>content</code> <strong>{String}</strong>: Content of a passwd file to parse.</li>\n<li><code>returns</code> <strong>{Array}</strong>: Array of user objects parsed from the content.</li>\n</ul>\n<h2>About</h2>\n<h3>Contributing</h3>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"../../issues/new\">please create an issue</a>.</p>\n<p>Please read the <a href=\"contributing.html\">contributing guide</a> for avice on opening issues, pull requests, and coding standards.</p>\n<h3>Building docs</h3>\n<p><em>(This document was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a> (a <a href=\"https://github.com/verbose/verb\">verb</a> generator), please don't edit the readme directly. Any changes to the readme must be made in <a href=\".verb.html\">.verb.md</a>.)</em></p>\n<p>To generate the readme and API documentation with <a href=\"https://github.com/verbose/verb\">verb</a>:</p>\n<pre><code class=\"language-sh\">$ npm install -g verb verb-generate-readme &#x26;&#x26; verb\n</code></pre>\n<h3>Running tests</h3>\n<p>Install dev dependencies:</p>\n<pre><code class=\"language-sh\">$ npm install -d &#x26;&#x26; npm test\n</code></pre>\n<h3>Author</h3>\n<p><strong>Brian Woodward</strong></p>\n<ul>\n<li><a href=\"https://github.com/doowb\">github/doowb</a></li>\n<li><a href=\"http://twitter.com/doowb\">twitter/doowb</a></li>\n</ul>\n<h3>License</h3>\n<p>Copyright © 2016, <a href=\"https://github.com/doowb\">Brian Woodward</a>.\nReleased under the <a href=\"LICENSE\">MIT license</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a>, v0.2.0, on October 19, 2016.</em></p>","docPath":"argo-ci/node_modules/parse-passwd/readme","proj":"argo-ci"}}

